          <div class="set-time" >
            <div style="font-weight: bold;padding-bottom: 20px">布控时间</div>
            <div class="time-ctrl"  v-for="(just,index) in contrlTimeData" :key = index>
              <span class="time-title">{{just.name}}</span>
              <div class="time-slider-box">
              <div v-for="(item,key) in just.dataArr" class="time-slider-in" :key="key" >
                <el-slider
                  class=" time-distance"
                  v-model="item.data"
                  range
                  label
                  style="width:60%"
                  :format-tooltip="_formatTime"
                  :max="144">
                </el-slider>
                <span class=" time-distance">{{_formatAllTime(item.data[0])}}-{{_formatAllTime(item.data[1])}}</span>
                <span class=" time-distance time-cursor" @click="_addTimeCtrl(just.dataArr)" v-if="!item.switchData">新增</span>
                <span class=" time-distance time-cursor" @click="_deleteTimeCtrl(just.dataArr,key)" v-if="item.switchData">删除</span>
                <span class=" time-distance time-cursor" @click="_previousData(index)" v-if="index!==0 &&!item.switchData">同上</span>
              </div>
              </div>
          </div>
          </div>
          <div class="set-time" >
            <div style="font-weight: bold;padding-bottom: 20px;padding-top: 20px">事件发生间隔</div>
            <el-input placeholder="请输入内容" v-model="IntervalTime"></el-input>
          </div>
        </div>
        <span slot="footer" class="dialog-footer">
