          <div class="dub-txt">设置列为可疑人员的阈值，低于阈值的照片将不会被列为可疑人员。</div>
          <div class="dub-line" style="padding-top: 30px">
            <span>出现频次：</span>
            <el-input
              placeholder="请输入内容"
              clearable>
            </el-input>
          </div>
          <div class="dub-txt">设置列为可疑人员的阈值，低于阈值的照片将不会被列为可疑人员。</div>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dubiousPerson = false">取 消</el-button>
            <el-button type="primary">确 定</el-button>
          </span>
      </el-dialog>
    </div>
</template>

<script>
  import APICONFIG from '@/config/api.js'
  export default{
    data() {
      return{
        typeArr: [], //已选中的人员类型
        allTypeAll: [], //所有人员类型
        netAPI: '', //接口api
        typeCtrlVal: '', //查询人脸布控的类别id
        typeCtrlName: '', //查询人脸布控的姓名字段
        IntervalTime: 20, //参数设置的间隔时间
        batchTimeId: '', //修改参数时间id
        faceCtrlData: [], //人脸布控数据
        ctrlDetails: {}, //布控人员详情
        ctrlSize: 10, //布控的展示页数
        ctrlTotal: 0, //布控展示总页数
        value1: '',
        dialogVisible: false,
        AssPerType: false,
        batchSet: false,
        photoList: false,
        dubiousPerson: false,
        value6: false,
        value9: [0, 144],
        timeArr: ['mon', 'tue', 'wed', 'thu', 'fri', 'sat', 'sun'],
        contrlTimeData: [{
          name: "星期一",
          dataArr: [{
            data: [0, 144],
            switchData: false
          }]
        },
        {
          name: "星期二",
