            switchData: false
          }]
        }, {
          name: "星期天",
          dataArr: [{
            data: [0, 144],
            switchData: false
          }]
        },
        ],
        tableData: [{
          date: '2016-05-02',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄',
          img: 'https://btc123.oss-cn-beijing.aliyuncs.com/04430b7c9a9f45ed9aa238ce8c98c561.jpg',
        }, {
          date: '2016-05-04',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1517 弄',
          img: 'https://btc123.oss-cn-beijing.aliyuncs.com/04430b7c9a9f45ed9aa238ce8c98c561.jpg'
        }, {
          date: '2016-05-01',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1519 弄',
          img: 'https://btc123.oss-cn-beijing.aliyuncs.com/04430b7c9a9f45ed9aa238ce8c98c561.jpg'
        }, {
          date: '2016-05-03',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1516 弄',
          img: 'https://btc123.oss-cn-beijing.aliyuncs.com/04430b7c9a9f45ed9aa238ce8c98c561.jpg'
        }, {
          date: '2016-05-03',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1516 弄',
          img: 'https://btc123.oss-cn-beijing.aliyuncs.com/04430b7c9a9f45ed9aa238ce8c98c561.jpg'
        }, {
          date: '2016-05-03',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1516 弄',
          img: 'https://btc123.oss-cn-beijing.aliyuncs.com/04430b7c9a9f45ed9aa238ce8c98c561.jpg'
        }, {
          date: '2016-05-03',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1516 弄',
          img: 'https://btc123.oss-cn-beijing.aliyuncs.com/04430b7c9a9f45ed9aa238ce8c98c561.jpg'
        }, {
          date: '2016-05-03',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1516 弄',
          img: 'https://btc123.oss-cn-beijing.aliyuncs.com/04430b7c9a9f45ed9aa238ce8c98c561.jpg'
        }, {
          date: '2016-05-03',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1516 弄',
          img: 'https://btc123.oss-cn-beijing.aliyuncs.com/04430b7c9a9f45ed9aa238ce8c98c561.jpg'
        }, {
          date: '2016-05-03',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1516 弄',
          img: 'https://btc123.oss-cn-beijing.aliyuncs.com/04430b7c9a9f45ed9aa238ce8c98c561.jpg'
        }],
        options: [{
          value: '选项1',
          label: '黄金糕'
        }, {
          value: '选项2',
          label: '双皮奶'
        }, {
          value: '选项3',
          label: '蚵仔煎'
        }, {
          value: '选项4',
          label: '龙须面'
        }, {
          value: '选项5',
          label: '北京烤鸭'
        }],
        startNum: 0,
        endNum: 144,
      }
    },
    created() {
      this.netAPI = APICONFIG().get("monitorSystem").faceControl;
    },
    mounted() {
      this._getCtrlData();
    },
    methods: {
        //格式化时间
      _formatTime(value) {
        return this._formatAllTime(value)
      },
      _formatAllTime(value) {
        var ho
        var mo
        var h = parseInt(value / 6);
        var m = value - (6 * h);
        if(h < 10) {
          ho = '0' + h
        }else {
          ho = h
        }
        if(m < 1) {
          mo = '0' + m
        }else {
          mo = m * 10
        }
        return ho + ":" + mo
      },
        //参数设置
      _addTimeCtrl(arr) {
        arr.push({
          data: [0, 144],
          switchData: true
        })
      },
      _deleteTimeCtrl(arr, key) {
        arr.splice(key, 1)
      },
      _previousData(key) {
        this.contrlTimeData[key].dataArr = [...this.contrlTimeData[key - 1].dataArr]
      },
        //获取布控人员
      _getCtrlData() {
        var param = {
          params: {
            "type": this.typeCtrlVal,
            "name": this.typeCtrlName,
            "pageSize": this.ctrlSize,
            "pageNo": 1
          }
        }
        this.$http.get(this.netAPI.faceCtrl, param).then(
          res => {
            if(res.body.status == 200) {
              this.faceCtrlData = res.body.data;
              this.ctrlTotal = res.body.total;
            }
          }
        )
      },
      //布控人员详情
      _getCtrlDetails(data) {
        this.dialogVisible = true;
        this.ctrlDetails = data;
        console.log(this.ctrlDetails)
      },
      //查询人脸布控设置参数
      _getFaceCtrlParam() {
        this.$http.get(this.netAPI.distributionFind).then(res => {
          this.typeArr = res.data.data.categories;
        })
      },
      _getAllParam() {
        this.AssPerType = true;
        this._getFaceCtrlParam();
        this.$http.get(this.netAPI.typeFind).then(res => {
          this.allTypeAll = res.data.data.map(item => {
            item.checked = false;
            item.newId = '';
            return item
          });
          this.typeArr.map(item => {
            for(var i = 0; i < this.allTypeAll.length; i++) {
              if(item.typeCode == this.allTypeAll[i].no) {
                this.allTypeAll[i].checked = true;
                this.allTypeAll[i].newId = item.id;
                break;
              }
            }
          })
        })
      },
      _chooseCtrlPer(val) {
        val.checked = !val.checked
      },
      //切换展示方式
      _photoList() {
        this.photoList = true;
        this.ctrlSize = 12;
        this._getCtrlData();
      },
      _lineList() {
        this.photoList = false;
        this.ctrlSize = 10;
        this._getCtrlData();
      },
      //修改布控人员或参数设置
      _saveCtrlType(type) {
        var valDa = {
          categories: []
        };
        this.allTypeAll.map(item => {
          if(item.checked) {
            valDa.categories.push({
              "id": item.newId,
              "typeCode": item.no,
              "typeName": item.name
            })
          }
        })
        this.$http.post(this.netAPI.distributionSave, valDa).then(res => {
          if(res.data.status == 200) {
            this.AssPerType = false;
            this.$message({
              message: '关联人员类型设置成功！',
              type: 'success'
            });
          }
        })
      },
      //参数设置
      _batchSet() {
        this.batchSet = true;
        this.$http.get(this.netAPI.timeDetail).then(res => {
          if(res.data.status == 200) {
            this.batchTimeId = res.data.data.id ? res.data.data.id : '';
            this.contrlTimeData.map((item, index) => {
              item.dataArr = res.data.data[this.timeArr[index]] ? JSON.parse(res.data.data[this.timeArr[index]]) : item.dataArr
            })
          }
        })
      },
      _saveCtrlTime() {
        var valDa = {
          "id": this.batchTimeId,
          "mon": JSON.stringify(this.contrlTimeData[0].dataArr),
          "tue": JSON.stringify(this.contrlTimeData[1].dataArr),
          "wed": JSON.stringify(this.contrlTimeData[2].dataArr),
          "thu": JSON.stringify(this.contrlTimeData[3].dataArr),
          "fir": JSON.stringify(this.contrlTimeData[4].dataArr),
          "sat": JSON.stringify(this.contrlTimeData[5].dataArr),
          "sun": JSON.stringify(this.contrlTimeData[6].dataArr),
          "interval": this.intervalTime
        };
        this.$http.post(this.netAPI.updateTime, valDa).then(res => {
          if(res.data.status == 200) {
            this.batchSet = false;
            this.$message({
              message: '参数设置成功设置成功！',
              type: 'success'
            });
          }
        })
      }
    }
  }
</script>

<style lang="scss" scoped>
#facePage{
    .page-name{
      font-size:20px;
      font-weight:400;
      color:rgba(43,51,62,1);
