      //陌生人员
      .Stranger-box{
        .face-choose{
          display: flex;
          justify-content:space-between;
          .left-box{
            display: flex;
            .search-b:not(:first-child){
              margin-left: 15px;
            }
            .last-time{
              display: flex;
              .name-txt{
                width:120px;
                height: 40px;
                line-height: 40px;
              }
            }
          }
          .right-box{
            height: 40px;
            align-items: center;
            img{
              margin: 10px 0 8px 8px;
            }
          }
        }
      }
      //可疑人员
      .Suspicious-per{
        .face-choose{
          display: flex;
          justify-content:space-between;
          .left-box{
            display: flex;
            .search-b:not(:first-child){
