            }
            .last-time{
              display: flex;
              .name-txt{
                width:50px;
                height: 40px;
                line-height: 40px;
              }
            }
          }
          .right-box{
            height: 40px;
            align-items: center;
            img{
              margin: 10px 0 8px 8px;
            }
          }
        }
        .table-box{
          margin-top: 20px;
        }
      }
    }
  //分页样式
  .page-box{
    display: flex;
    justify-content: space-between;
    padding-top: 20px;
    .t-num{
      color: rgba(147,147,147,1);
      font-size:14px;
      height: 30px;
