    }
  }
  //详情弹框样式
  .details-box{
    display: flex;
    justify-content: start;
    padding:30px 50px;
    box-sizing: border-box;
    img{
      width: 160px;
      height: 160px;
    }
    .inf-box{
      margin-left:60px;
      margin-top: -16px;
      >p{
      display: flex;
        span:first-child{
          display: inline-block;
